<template>
  <div class="conteiner-fluid">
    <div class="content">
    	<h2 class="text-center">{{ desc }}</h2>
    	<form id="signup-form" class="signup-form" v-on:submit.prevent="processForm">
			<div class="form-group" :class="{opacity: !show_textArea}">
				<!-- name -->
				<div class="field">
					<label class="label" :class="{ 'to_top': !name_up }">name</label>
					<input type="text" class="input" v-model="name_input" @focus="name_up = !name_up" @blur="name_up = true">
				</div>
				<!-- email -->
				<div class="field">
					<label class="label" :class="{ 'to_top': !email_up }">e-mail</label>
					<input type="e-mail" class="input" v-model="email_input" @focus="email_up = !email_up" @blur="email_up = true">
				</div>
				<transition name="fade-left">
					<button 
						v-if="email_input !== 'e-mail' && name_input !== 'name'"
						@click="show_textArea = !show_textArea"
						class="form-button"
						>
						<i class="fas fa-arrow-alt-circle-right"></i>	
					</button>
				</transition>
			</div>

			<transition name="fade-left">
				<div class="textArea" v-if="!show_textArea">
					<div class="field">
						<textarea class="w-100" name="text"></textarea>
					</div>
					<!-- submit button -->
					<div class="field has-text-right">
						<button @click="show_textArea = !show_textArea" type="submit" class="button is-danger">Отправить</button>
					</div>
				</div>
			</transition>
    	</form>
    </div>
  </div>
</template>

<script>
export default {
	data () {
	    return {
	      	desc: 'Напишите мне',
			name_up: true,
			email_up: true,
			show_textArea: true,
			name_input: 'name',
			email_input: 'e-mail'
	    }
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
$dark: #2c3e50
$orange: #FF6700
h1, h2
  color: $dark
  font-weight: normal
  margin: 30px 0 40px

p
  font-size: 27.5pt
.field
  margin-top: 15px
  .input, textarea
    border: 1px solid #e5e5e5
    border-radius: 0
    -webkit-appearance: none
    -webkit-border-radius: 0
    box-shadow: none
    -webkit-box-shadow: none
    &:focus
      border: 1px solid $orange
  .button
    background: $orange
    border: 0
    box-shadow: none
    -webkit-box-shadow: none
    color: #fff
    padding: 6px 0
    width: 120px
</style>



