<template>
	<div class="projects">
		<div class="container height_full pt-5">
			<div class="conteiner-fluid pt-4">
				<div class="row justify-content-center">
						
					<div v-for="(example, exampleID) in image_info"
						 :key="exampleID"
						 class="col-12 col-md">
						<div class="photoWrapper mb-4">
							<button class="showBtn" v-on:click="example.visible = !example.visible">
								<i class="far fa-eye"></i>
								{{ buttonName }}
							</button>
							<div class="image bg-violet">
								<img width="457" :src="example.src" alt="photo">
							</div>
							<transition name="example-animation">
								<div class="aboute-project fixed-top" v-if="example.visible">
									<div class="container-fluid">
										<button class="showBtn" v-on:click="example.visible = !example.visible">
											<i class="fas fa-times"></i>
										</button>
										<div class="row">
											<div class="col-12 col-md-5 col-lg-4 mb-sm-4">
												<h2 class="text-center">{{ example.title }}</h2>
												<p>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Corporis, facere repudiandae. Velit facere porro, consequuntur et possimus quod culpa distinctio vel, aliquid accusamus, at quaerat nihil nulla. Minima, commodi beatae?</p>
												<div class="button-look"><a :href="example.link_to">перейти</a></div>	
											</div>
											<div class="col-12 col-md-7 col-lg-8 pb-4">
												<img :src="example.src" alt="photo">
											</div>
										</div>
									</div>
								</div>	
							</transition>
						</div>
					</div>

				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'Carousel',
	data () {
		return {
			header: 'Мои работы',
			buttonName: 'Посмотреть',
			image_info: [
				{
					src: require('../assets/images/Screenshot_2019-01-10 Y S.jpg'),
					link_to: 'https://uhonogo.github.io/buildcomputer',
					title: 'Дизайн-студия',
					visible: false
				},
				{
					src: require('../assets/images/portfolio.png'),
					link_to: 'https://uhonogo.github.io/flowers',
					title: 'Продажа цветов',
					visible: false
				},
				{
					src: require('../assets/images/house.jpg'),
					link_to: 'https://uhonogo.github.io/house',
					title: 'Продажа домов',
					visible: false
				},
				{
					src: require('../assets/images/htmb.jpg'),
					link_to: 'https://uhonogo.github.io/star-toys',
					title: 'Детские игрушки',
					visible: false
				},
				{
					src: require('../assets/images/template_screenshot.jpg'),
					link_to: 'https://uhonogo.github.io/butterbroad',
					title: 'Лендинг',
					visible: false
				},
				{
					src: require('../assets/images/landing.jpg'),
					link_to: 'https://uhonogo.github.io/travel',
					title: 'Путешествия',
					visible: false
				}
			]
		}
	}
}
</script>



