<template>
  <header class="header-block" :class="{ scrolled: scrollPosition > 0}">
	  <div class="block">
		<parallax :fixed="false" class="position-absolute">
		<img src="../assets/images/mpc.jpg" alt="gdgdg">
		</parallax>
		<div class="container-fluid height_full dark_background" slot="loading">
			<div class="row align-items-center height_full">
				<div class="col">
					<div class="myself">
					</div>
				
					<h1 class="text-center">
						{{ header }}
						<span class="second_row d-block">{{ desc }}</span>
					</h1>

				</div>
			</div>
		</div>
	  </div>
  </header>
</template>

<script>
import Parallax from 'vue-parallaxy'
export default {
  components: {
    Parallax
  },
  name: 'Aboute',
  data () {
    return {
	  scrollPosition: null,
	  windowWidth: 0,
      header: 'Я - Юрий Щербаченко',
      desc: 'html-верстальщик, фрилансер'
    }
  },
  mounted() {
	this.$nextTick(function() {
	  window.addEventListener('resize', this.getWindowWidth);	
	  window.addEventListener('scroll', this.updateScroll);

	  this.getWindowWidth()
	  this.updateScroll()
	})
  },
  methods: {
  	getWindowWidth(event) {
  	  this.windowWidth = document.documentElement.clientWidth;
  	},
  	updateScroll: function() {
  	  this.scrollPosition = window.scrollY
  	}
  },
  beforeDestroy() {
  	window.removeEventListener('resize', this.getWindowWidth);
  	window.removeEventListener('scroll', this.updateScroll);
  }
}

</script>



